* {
  box-sizing: border-box;
}

body {
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
  color: #212121;
  background-color: #fafafa;
  font-family: -apple-system, BlinkMacSystemFont, Segoe UI, Roboto, Oxygen, Ubuntu, Cantarell, Open Sans, Helvetica Neue, sans-serif;
  line-height: 1.5;
}

.country-list {
  margin: 0;
  padding: 0;
  list-style: none;
}

.country-list li:nth-child(2n+1) {
  background-color: #f1f1f1;
}

.country-list li {
  cursor: pointer;
  padding: 10px;
  box-shadow: 0 1px 1px rgba(0, 0, 0, .2);
}

.country-list li:hover {
  background-color: #f1f1f1;
}

.country-info {
  margin-top: 20px;
  padding: 20px;
  box-shadow: 0 1px 1px rgba(0, 0, 0, .2);
}

.country__Flag {
  margin-right: 5px;
}

#search-box {
  width: 80%;
  height: 50px;
  border: none;
  border-radius: 8px;
  margin: 5% auto 0;
  padding: 10px;
  font-size: 16px;
  transition: box-shadow .3s ease-in-out;
  display: block;
  box-shadow: 0 8px 16px rgba(0, 0, 0, .1), inset 0 4px 4px rgba(255, 255, 255, .2), inset 0 -4px 4px rgba(255, 255, 255, .2);
}

#search-box:focus {
  outline: none;
  box-shadow: 0 8px 16px rgba(0, 0, 0, .2), inset 0 4px 4px rgba(255, 255, 255, .1), inset 0 -4px 4px rgba(255, 255, 255, .1);
}

#search-box::-ms-input-placeholder {
  opacity: .5;
  color: #9ba4b5;
}

#search-box::placeholder {
  opacity: .5;
  color: #9ba4b5;
}

